.fade-list-enter-active,.fade-list-leave-active{transform:translate3d(30px, 0, 0)}.fade-list-enter,.fade-list-leave-to{transition:all .3s ease;opacity:0}.upload-btn-container{display:inline-block;width:auto;height:32px;line-height:32px;font-size:14px}.upload-btn-container .upload-btn-box{position:relative;border-radius:2px;color:white;padding:0 10px;display:inline-block;background-color:#409eff;border-color:#409eff}.upload-btn-container .upload-btn-box:active,.upload-btn-container .upload-btn-box:hover{background-color:#66b1ff}.upload-btn-container .upload-btn{opacity:0;position:absolute;top:0;left:0;width:100%;height:100%}
